import React, { Component, lazy, Suspense } from "react";
import { Link } from "react-router-dom";
import {
  setTampungCiok,
  settampungRongsok,
} from "../actions/pembayaranSupplierAction";
import FormTolakanPembayaranHutangSupplier from "./Component/FormTolakanPembayaranHutangSupplier";
import Skeleton from "react-loading-skeleton";
import Swal from "sweetalert2";
import { connect } from "react-redux";
import Axios from "axios";
import { getUUID } from "../actions/fakturAction";
import PrintNota from "../Stocking/Component/PrintNota";
import { warning } from "../Alert";

const FormCiok = lazy(() => import("./Component/FormCiok"));
const FormRongsok = lazy(() => import("./Component/FormRongsok"));
const maptoState = (state) => {
  return {
    noFaktur: state.provinsi.noFaktur,
    username: state.provinsi.username,
    tampungCiok: state.provinsi.tampungCiok,
    tampungRongsok: state.provinsi.tampungRongsok,
    totalCiok: state.provinsi.totalCiok,
    totalRongsok: state.provinsi.totalRongsok,
    totalBrutoCiok: state.provinsi.totalBrutoCiok,
    totalBrutoRongsok: state.provinsi.totalBrutoRongsok,
  };
};
const server = process.env.REACT_APP_BACKEND_URL_NON_MASTER;

class TolakanHutangSupplier extends Component {
  constructor(props) {
    super(props);
    this.state = {};
  }

  handleSubmitCiok(data) {
    var kode = data.kodeBarang;
    var split = kode.split("|");
    var isi = {
      kodeBarang: split[0],
      netto: data.netto,
      bruto: data.bruto,
      kadarTransaksi: data.kadarTransaksi,
    };
    var ciokTemp = JSON.parse(localStorage.getItem("ciokTemp")) || [];
    ciokTemp.push(isi);
    localStorage.setItem("ciokTemp", JSON.stringify(ciokTemp));
    var alatBayarBarang =
      JSON.parse(localStorage.getItem("alatBayarBarang")) || [];
    alatBayarBarang.push(isi);
    localStorage.setItem("alatBayarBarang", JSON.stringify(alatBayarBarang));
    this.props.dispatch(setTampungCiok());
    Swal.fire({
      position: "top-end",
      icon: "success",
      text: "Data Berhasil Ditambahkan!",
      showConfirmButton: false,
      timer: 1500,
    });
  }
  handleSubmitRongsok(data) {
    var kode = data.kodeBarang;
    var split = kode.split("|");
    var isi = {
      kodeBarang: split[0],
      netto: data.netto,
      bruto: data.bruto,
      kadarTransaksi: data.kadarTransaksi,
    };
    var rongsokTemp = JSON.parse(localStorage.getItem("rongsokTemp")) || [];
    rongsokTemp.push(isi);
    localStorage.setItem("rongsokTemp", JSON.stringify(rongsokTemp));
    var alatBayarBarang =
      JSON.parse(localStorage.getItem("alatBayarBarang")) || [];
    alatBayarBarang.push(isi);
    localStorage.setItem("alatBayarBarang", JSON.stringify(alatBayarBarang));
    this.props.dispatch(settampungRongsok());
    Swal.fire({
      position: "top-end",
      icon: "success",
      text: "Data Berhasil Ditambahkan!",
      showConfirmButton: false,
      timer: 1500,
    });
  }
  componentDidMount() {
    this.props.dispatch(setTampungCiok());
    this.props.dispatch(settampungRongsok());
    this.props.dispatch(getUUID());
  }
  handlSubmit(data) {
    if (data.keterangan === undefined) {
      warning("Mohon isi Keterangan");
      return false;
    } else if (data.noBon === undefined) {
      warning("Mohon isi Nomor Bon");
      return false;
    } else if (data.supplier === undefined) {
      warning("Mohon isi supplier");
      return false;
    } else if (data.tanggal === undefined) {
      warning("Mohon isi Tanggal");
      return false;
    } else if (localStorage.getItem("alatBayarBarang") === null) {
      warning("Mohon isi alat bayar");
      return false;
    }
    var bayarBarang = JSON.parse(localStorage.getItem("alatBayarBarang"));
    var split = data.supplier ? data.supplier : "DEFAULT | DEFUALT";
    var supplier = split.split("|");
    var isi = {
      alatBayarBarang: bayarBarang,
      keterangan: data.keterangan,
      totalAlatBayar: {
        totalRsk: parseFloat(this.props.totalRongsok).toFixed(3),
        totalCt: parseFloat(this.props.totalCiok).toFixed(3),
        totalTunaiRp: 0,
        totalTrfRp: 0,
        totalPutusGr: 0,
        tipeData: "TOLAKAN SUPPLIER",
        totalPutusRp: 0,
      },
      kodeSupplier: supplier[0],
      namaSupplier: supplier[1],
      noBon: data.noBon,
      // noBonBayar: data.noBayar,
      noFaktur: this.props.noFaktur,
      tanggal: data.tanggal,
      footerDocument: {
        inputBy: this.props.username, //INPUT BY DEFAULT
      },
    };
    console.log(JSON.stringify(isi));
    // INISIALISASI AUTOTABLE
    const tableRows = [];
    var ciok = JSON.parse(localStorage.getItem("ciokTemp")) || [];
    ciok.forEach((ticket, i) => {
      const ticketData = [
        "Ciok",
        ticket.bruto,
        "",
        ticket.kadarTransaksi,
        ticket.netto,
        "",
        "",
      ];
      // push each tickcet's info into a row
      tableRows.push(ticketData);
    });
    var rongsok = JSON.parse(localStorage.getItem("rongsokTemp")) || [];
    rongsok.forEach((ticket, i) => {
      const ticketData = [
        "Rongsok",
        ticket.bruto,
        "",
        ticket.kadarTransaksi,
        ticket.netto,
        "",
        "",
      ];
      // push each tickcet's info into a row
      tableRows.push(ticketData);
    });
    console.log(JSON.stringify(tableRows));
    var columTable = [
      "Jenis Pembayaran",
      "Gross Bayar",
      "Harga",
      "Kadar",
      "Netto",
      "Pkg",
      "Gw+Pkg",
    ];
    // AKHIR INISIALISASI AUTOTABLE
    Axios.post(server + "spp/dc/tlkbyr", isi)
      .then(() =>
        Swal.fire({
          position: "top-end",
          icon: "success",
          text: "Data Berhasil Ditambahkan!",
          showConfirmButton: false,
          timer: 1500,
        })
          .then(() => localStorage.clear())
          .then(() =>
            PrintNota(
              "Tanggal",
              data.tanggal,
              "",
              "",
              "Nomor \nPembayaran",
              data.noBon,
              "",
              "",
              "ADMIN",
              data.tanggal,
              "",
              columTable,
              "NOTA TOLAKAN PEMBAYARAN SUPPLIER",
              tableRows
            )
          )
      )
      .catch((err) =>
        Swal.fire({
          position: "top-end",
          icon: "error",
          text: err,
          showConfirmButton: false,
          timer: 1500,
        })
      );
  }
  render() {
    return (
      <div className="container-fluid">
        <div className="col-lg-12 col-d-12">
          <ol className="breadcrumb mb-2">
            <Link to="KlasifikasiSupplier">
              <button
                type="button"
                className="btn btn-secondary"
                onClick={() => localStorage.clear()}
              >
                <i className="fa fa-angle-left mr-3" aria-hidden="true"></i>{" "}
                Back
              </button>
            </Link>
          </ol>
          <div className="card">
            <div className="card-body">
              <FormTolakanPembayaranHutangSupplier
                noFaktur={this.props.noFaktur}
                tampungCiok={this.props.tampungCiok}
                tampungRongsok={this.props.tampungRongsok}
                totalCiok={this.props.totalCiok}
                totalBrutoCiok={this.props.totalBrutoCiok}
                totalRongsok={this.props.totalRongsok}
                totalBrutoRongsok={this.props.totalBrutoRongsok}
                onSubmit={(data) => this.handlSubmit(data)}
              />
            </div>
          </div>
        </div>
        <div
          id="ciokModal"
          className="modal fade"
          tabIndex="-1"
          role="dialog"
          aria-labelledby="primary-header-modalLabel"
          aria-hidden="true"
        >
          <div className="modal-dialog modal-lg">
            <div className="modal-content">
              <div className="modal-header modal-colored-header bg-primary">
                <h4 className="modal-title" id="primary-header-modalLabel">
                  Tambah Data Ciok
                </h4>
                <button
                  id="dismiss_modal"
                  name="dismiss_modal"
                  type="button"
                  className="close"
                  data-dismiss="modal"
                  aria-hidden="true"
                >
                  ×
                </button>
              </div>
              <div className="modal-body">
                <Suspense
                  fallback={
                    <div>
                      <Skeleton width={760} height={421} />
                    </div>
                  }
                >
                  <FormCiok
                    data={this.props.tampungCiok}
                    onSubmit={(data) => this.handleSubmitCiok(data)}
                  />
                </Suspense>
              </div>
              <div className="modal-footer">
                <button
                  type="button"
                  className="btn btn-light"
                  data-dismiss="modal"
                >
                  Close
                </button>
              </div>
            </div>
          </div>
        </div>
        <div
          id="rongsokModal"
          className="modal fade"
          tabIndex="-1"
          role="dialog"
          aria-labelledby="primary-header-modalLabel"
          aria-hidden="true"
        >
          <div className="modal-dialog modal-lg">
            <div className="modal-content">
              <div className="modal-header modal-colored-header bg-primary">
                <h4 className="modal-title" id="primary-header-modalLabel">
                  Tambah Data Rongsok
                </h4>
                <button
                  id="dismiss_modal"
                  name="dismiss_modal"
                  type="button"
                  className="close"
                  data-dismiss="modal"
                  aria-hidden="true"
                >
                  ×
                </button>
              </div>
              <div className="modal-body">
                <Suspense
                  fallback={
                    <div>
                      <Skeleton width={760} height={420} />
                    </div>
                  }
                >
                  <FormRongsok
                    onSubmit={(data) => this.handleSubmitRongsok(data)}
                    data={this.props.tampungRongsok}
                  />
                </Suspense>
              </div>
              <div className="modal-footer">
                <button
                  type="button"
                  className="btn btn-light"
                  data-dismiss="modal"
                >
                  Close
                </button>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default connect(maptoState, null)(TolakanHutangSupplier);
