import React, { Component, lazy, Suspense } from "react";
import { connect } from "react-redux";
import { Field, formValueSelector, reduxForm } from "redux-form";
import axios from "axios";
import Skeleton from "react-loading-skeleton";
const AccordCiok = lazy(() => import("../Element/AccordCiok"));
const AccordRongsok = lazy(() => import("../Element/AccordRongsok"));

const renderField = ({
  input,
  label,
  type,
  limit,
  readOnly,
  meta: { touched, error, warning },
}) => (
  <div className="form-group">
    <label htmlFor="" className="text-black">
      {label}
    </label>
    <input
      {...input}
      type={type}
      className="form-control"
      min="0"
      step="0.001"
      maxLength={limit}
      readOnly={readOnly}
    />
    {touched &&
      ((error && <p className="invalid-feedback">{error}</p>) ||
        (warning && <p>{warning}</p>))}
  </div>
);

const server = process.env.REACT_APP_BACKEND_URL;
class FormTolakanPembayaranHutangSupplier extends Component {
  constructor(props) {
    super(props);
    this.state = {
      listSupplier: [],
    };
  }

  componentDidMount() {
    axios
      .get(server + "suppliers")
      .then((res) =>
        this.setState({
          listSupplier: res.data,
        })
      )
      .catch((err) => console.log(err));
  }
  setTotal() {
    this.props.change("totalBruto", this.props.sumBruto);
    this.props.change("totalNetto", this.props.sumNetto);
  }
  render() {
    return (
      <>
        <Suspense fallback={<Skeleton width={"100%"} height={50} count={2} />}>
          <div className="row">
            <AccordCiok
              totalCiok={this.props.totalCiok}
              totalBrutoCiok={this.props.totalBrutoCiok}
            />
            <AccordRongsok
              totalRongsok={this.props.totalRongsok}
              totalBrutoRongsok={this.props.totalBrutoRongsok}
            />
          </div>
        </Suspense>
        <div className="row mb-3">
          <div className="col-lg-6">
            <Field
              name="totalBruto"
              label="Total Bruto"
              component={renderField}
              readOnly={true}
              onBlur={this.setTotal()}
            />
          </div>
          <div className="col-lg-6">
            <Field
              name="totalNetto"
              label="Total Netto"
              component={renderField}
              readOnly={true}
              onBlur={this.setTotal()}
            />
          </div>
        </div>
        <form onSubmit={this.props.handleSubmit}>
          <div className="row">
            <div className="col-md-4 col-lg-4">
              <Field
                name="tanggal"
                label="Tanggal"
                type="date"
                component={renderField}
              />
            </div>
            <div className="col-md-4 col-lg-4">
              <label htmlFor="">Supplier</label>
              <Field
                name="supplier"
                className="form-control"
                component="select"
              >
                <option value="DEFAULT" key="DEFAULT">
                  SILAHKAN PILIH
                </option>
                {this.state.listSupplier.map((list) => (
                  <option
                    value={list.kodeSupplier + "|" + list.namaSupplier}
                    key={list.kodeSupplier}
                  >
                    {list.namaSupplier}
                  </option>
                ))}
              </Field>
            </div>
            <div className="col-lg-4 col-md-4">
              <Field
                name="noBon"
                component={renderField}
                type="text"
                label="No Bon Bayar"
              />
            </div>
            <div className="col-md-4 col-lg-4">
              <Field
                name="noBayar"
                label="No Bayar"
                type="text"
                component={renderField}
                onLoad={this.props.change("noBayar", this.props.noFaktur)}
                readOnly={true}
              />
            </div>
            <div className="col-lg-4 col-md-4">
              <Field
                name="keterangan"
                label="Keterangan"
                type="text"
                component={renderField}
              />
            </div>
            <div className="col-lg-12 col-md-12">
              <div className="text-right">
                <button className="btn btn-primary" type="submit">
                  Simpan
                </button>
              </div>
            </div>
          </div>
        </form>
      </>
    );
  }
}

FormTolakanPembayaranHutangSupplier = reduxForm({
  form: "FormTolakanPembayaranHutangSupplier",
  enableReinitialize: true,
})(FormTolakanPembayaranHutangSupplier);
const selector = formValueSelector("FormTolakanPembayaranHutangSupplier"); // <-- same as form name
export default connect((state) => {
  const { harga, cash, transfer } = selector(
    state,
    "harga",
    "cash",
    "transfer"
  );
  var totalBrutoCiok = JSON.parse(localStorage.getItem("totalBrutoCiok"))
    ? JSON.parse(localStorage.getItem("totalBrutoCiok"))
    : 0;
  var totalBrutoRongsok = JSON.parse(localStorage.getItem("totalBrutoRongsok"))
    ? JSON.parse(localStorage.getItem("totalBrutoRongsok"))
    : 0;
  var totalNettoCiok = JSON.parse(localStorage.getItem("totalCiok"))
    ? JSON.parse(localStorage.getItem("totalCiok"))
    : 0;
  var totalNettoRongsok = JSON.parse(localStorage.getItem("totalRongsok"))
    ? JSON.parse(localStorage.getItem("totalRongsok"))
    : 0;
  return {
    cashNetto: (parseFloat(cash) / parseFloat(harga)).toFixed(3),
    transferNetto: (parseFloat(transfer) / parseFloat(harga)).toFixed(3),
    sumBruto: (
      parseFloat(totalBrutoCiok) + parseFloat(totalBrutoRongsok)
    ).toFixed(3),
    sumNetto: (
      parseFloat(totalNettoCiok) + parseFloat(totalNettoRongsok)
    ).toFixed(3),
  };
})(FormTolakanPembayaranHutangSupplier);
